@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.App {
  text-align: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  /* background-color: #ccc; */
  font-family: "Lato", sans-serif;
}
h1 {
  text-transform: uppercase;
}
.active,
.done {
  width: 90vw;
  padding: 5px 20px;
  margin: 5px 20px 0 20px;
  max-width: 700px;
  border: 1px solid black;
  box-shadow: 3px 4px 12px 0px rgba(0, 0, 0, 0.78);
}
.active h3,
.done h3 {
  text-transform: uppercase;
}
.active button,
.done button {
  margin-right: 2px;
  margin-left: 5px;
  padding: 0 5px;
  text-transform: uppercase;
}
.active li,
.done li {
  border-bottom: 1px solid #000;
  padding: 10px 0;
}
/* .active li p, */
.active li span,
.done li span {
  font-size: 12px;
}
.done h3 em {
  color: red;
}
.active li,
.done li {
  list-style: none;
}

.form {
  border: 1px solid black;
  margin: 5px 20px;
  padding: 20px;
  font-family: "Lato", sans-serif;
  text-transform: uppercase;
  box-shadow: 3px 4px 12px 0px rgba(0, 0, 0, 0.78);
  width: 90vw;
  max-width: 700px;
}
.form input {
  outline: none;
  margin: 10px;
}
.form input[type="text"] {
  font-size: 20px;
  line-height: 30px;
  height: 30px;
  width: 70%;
}

.form button {
  width: 150px;
  height: 30px;
  text-transform: uppercase;
}

